'use client'

let javonetPromise: Promise<unknown> | null = null

export const loadJavonet = async () => {
  if (typeof window === 'undefined') {
    throw new Error('Javonet SDK is only available in the browser.')
  }
  if (!javonetPromise) {
    // Dynamic import - javonet-nodejs-sdk is client-side only
    javonetPromise = import('javonet-nodejs-sdk')
  }
  return javonetPromise
}
